___CSS_LOADER_EXPORT___.push([module.id, `body {
  background-color: rgb(63, 63, 63);
  margin: 0;
  padding: 0;
  box-sizing: border-box;

  display: flex;
  justify-content: center;

  font-family: Arial, Helvetica, sans-serif;
}

main {
  /* background-color: rgb(41, 184, 103); */
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  width: 50%;
  height: 60vh;

  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  padding: 20px;

  margin-top: 2%;
  border-radius: 10px;
}

input {
  font-size: 1rem;
  /* border-radius: 8px; */
}
button {
  font-size: 1rem;
}

.weatherDisplay {
  /* height: 50vh; */
  width: 90%;
  padding: 20px;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.5);
  color: white;

  font-size: 1.2rem;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.locName {
  font-size: 1.5rem;
}

@media (width<500px) {
  main {
    width: 100%;
    height: 80vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 10px;

    margin: 10% 5%;
    /* filter: blur(1px); */
    /* backdrop-filter: blur(20px); */
  }
}

.timeAndTemp {
  display: flex;
  justify-content: space-between;
}
